@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:  #2D2D2D;
}

html{
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#dream-box{
  background-color: #191919;
  width: 60%;
  margin: 0 auto;
  padding: 10px 20px 40px;
  margin-bottom: 30px;
  position: relative;
  box-shadow: 0 3px 5px black;
  text-align: left;
}

#dream-box > span{
  color: brown;
  font-size: 18px;
  position: absolute;
  right: 10px;
  top: 10px;
}

#dream-box > span:hover{
  cursor: pointer;
  transform: scale(1.2);
  transition: 0.3s;
  color: red;
}

#flexer{
  display: flex;
  flex-direction: column-reverse;
}

#title-box{
  color: #3B444F;
  font-size: 20px;
  border: none;
  width: 80%;
  background-color: transparent;
  font-weight: bold;
}

#txt-area{
 background-color: transparent;
 border: none;
 padding-top: 10px;
 margin-top: 20px;
 font-family: Arial, Helvetica, sans-serif;
 font-size: 18px;
 width: 80%;
 min-height: 180px;
 color: snow;
}

.fa-edit{
  position:absolute;
  right: 40px;
  top: 10px;
  font-size: 23px;
  padding-top: 3px;
  color: brown;
}

.fa-check-circle{
  display: none;
  position:absolute;
  right: 40px;
  top: 30%;
  font-size: 80px;
  padding-top: 2px; 
}

.fa-edit:hover{
  cursor: pointer;
  transform: scale(1.1);
  transition: 0.3s;
  color: gold;
}

.fa-check-circle:hover{
  cursor: pointer;
  transform: scale(1.1);
  transition: 0.3s;
  color: #385E3C;
}

#typ{
  position: absolute;
  right: 10px;
  bottom: 0px;
  color: goldenrod;
  font-weight: bold;
}

@media (max-width: 700px) {
  #dream-box{
    padding: 10px 10px 40px;
    box-sizing: border-box;
    width: 90%;
  }

  #title-box{
    width: 100%;
    box-sizing: border-box;
  }

  #txt-area{
    padding-top: 0px;
    font-size: 16px;
    width: 100%;
    min-height: 180px;
    color: snow;
    box-sizing: border-box;
   }

   #dream-box > span{
    position: static;
    margin: 0 10px;
    font-size: 23px;
    padding-top: 4px;

  }

   .fa-edit{
    position: static;
    margin-left: 20px;
    float: right;
    font-size: 30px;
  }
  #typ{
    position: static;
    color: goldenrod;
  }

  
  .fa-check-circle{
    display: none;
    position:absolute;
    right: 40px;
    top: 30%;
    font-size: 80px;
    padding-top: 2px; 
  }

  .fa-check-circle{
    position:absolute;
    right: 40px;
    top: 70%;
    color:  #385E3C;
  }
}
.radio-container{
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}

.radioo{
  cursor: pointer;
}

label{
  font-size: 16px;
}
.App {
  text-align: center;
}

body{
  font-family: 'Open Sans', sans-serif;
}

#header{
  background-color: #191919;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  margin-bottom: 0px;
  box-shadow: 0 4px 5px black;
}

.search {
  position: relative;
  display: flex;
  width: 40%;
  margin: 0 auto;
}

.searchTerm {
  width: 100%;
  border: none;
  padding: 10px;
  height: 20px;
  outline: none;
  color: white;
  background-color: #3B444F;
  box-shadow: 1px 1px black;
  font-size: 16px;
}

.fa-search {
  position: absolute;
  top:  10px;
  right: 10px;
  color: gray;
  cursor: pointer;
  font-size: 20px;
}

#slide-section{
  display: none;
  background-color: #191919;
  padding-bottom: 10px;
  box-shadow: 0 4px 5px black;
}

#add-form{
  background-color: #2D2D2D;
  width: 400px;
  margin: 0 auto 10px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}

#add-form > *{
  margin-bottom: 10px;
  background-color:   #191919;
  border: none;
  padding:5px;
  color: snow;
}

#add-form > *:nth-child(3){
  background-color:  transparent;
  margin-bottom: 15px;
  color: snow;
}

#add-form > *:first-child::-webkit-input-placeholder, #add-form > *:nth-child(2)::-webkit-input-placeholder {
  color: snow;
}

#add-form > *:first-child:-ms-input-placeholder, #add-form > *:nth-child(2):-ms-input-placeholder {
  color: snow;
}

#add-form > *:first-child::placeholder, #add-form > *:nth-child(2)::placeholder {
  color: snow;
}

#add-form > *:last-child{
  background-color: #3B444F;
  color: white;
  font-weight: bolder;
  letter-spacing: 1px;
  cursor: pointer;
  box-shadow: 0 2px 4px black;
  padding: 10px 0;
}

#add-form > *:last-child:hover{
  transform: scale(1.01);
  transition: 0.3s;
}

#flexer{
  margin-top: 30px;
  display: flex;
  flex-direction: column-reverse;
}

#validRadio{
  background-color: transparent;
  color: red;
  font-size: 14px;
  display: none;
  border-radius: 5px;
  margin-bottom: 30px;
}

#validRadio > i{
  margin-right: 5px;

}

@media (max-width: 700px) {
  .search {
    width: 100%;
  }

  #add-form{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 20px;
    box-sizing: border-box;
  }
  
  
  
}
#slider{
  margin: 0;
  
}

#Slider-btn{
  background-color: #191919;
  width: 250px;
  font-size: 16px;
  color: gray;
  padding: 10px;
  font-weight: bold;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  align-items: center;
  border: none;
  box-shadow: 0px 4px 5px black;
}



#Slider-btn:hover{
  transform: scale(1.03);
  transition: 0.3s;
  cursor: pointer;
  color: white;
}

@media (max-width: 700px) {
  #Slider-btn{
    width: 90%;
  }
}
